.details__tab {
  flex-grow: 1;
  background-color: white;
  border-radius: 0px;
  border: 1px solid black;
  width: 100%;
  height: 60px;
  font-size: 18px;

  .details__tab:hover {
    cursor: pointer;
  }

  @media all and (max-width:728px) {
    display: none;
  }
}
.details__tab--selected {
  border-bottom: 0px;
}
