.app__footer {
    height: 40px;
    max-width: 320px;
    display: flex;
    justify-content: space-around;
    margin-left: auto;
    margin-right: auto;
}

.app__footer-logo {
    display: flex;
    align-items: center;
}

.footer-logo-image {
    height: 18px;
    width: auto;
}

.app__footer-students {
    display: flex;
    justify-content: space-around;
}

.app__footer-student-name {
    margin-left: 3px;
}

.app__footer-student {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-decoration: none;
    color: #838181;
    font-size: 10px;
    margin-left: 6px;
    display: flex;
    align-items: center;
}

.app__footer-student-lastname {
    display: none;
}

@media all and (min-width: 768px) {
    .app__footer {
        height: 55px;
        max-width: 768px;
    }
    .footer-logo-image {
        height: 27px;
    }
    .app__footer-student {
        font-size: 13px;
        margin-left: 28px;
    }
    .app__footer-student-lastname {
        display: inline-block;
    }
}

@media all and (min-width: 1200px) {
    .app__footer {
        height: 60px;
        max-width: 1200px;
    }
    .footer-logo-image {
        height: 40px;
    }
    .app__footer-student-name {
        margin-left: 5px;
    }
    .app__footer-student {
        font-size: 16px;
        margin-left: 60px;
    }
}
